.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* styles.css */
.circle-3d-button {
  width: 100px; /* กำหนดความกว้าง */
  height: 100px; /* กำหนดความสูง */
  line-height: 100px; /* ให้ข้อความอยู่ตรงกลางแนวตั้ง */
  text-align: center; /* ให้ข้อความอยู่ตรงกลางแนวนอน */
  color: rgb(243, 211, 9); /* สีตัวอักษร */
  background-color: #007bff; /* สีพื้นหลังปุ่ม */
  border: none;
  border-radius: 50%; /* ทำให้ปุ่มมีรูปทรงวงกลม */
  box-shadow: 0 8px #0056b3; /* สร้างเงาเพื่อลักษณะ 3D */
  font-size: 20px;
  cursor: pointer;
  outline: none;
}

.circle-3d-button:active {
  /* ปรับเมื่อปุ่มถูกกด */
  transform: translateY(4px); /* ขยับปุ่มลงเล็กน้อยเมื่อถูกกด */
  box-shadow: 0 2px #0056b3; /* ลดขนาดเงาเมื่อปุ่มถูกกด */
}

